<template>
  <button
    class="mdui-btn"
    :class="mduiClass"
    :disabled="disabled"
    @click="$emit('click', $event)"
    @mouseenter="$emit('mouseenter', $event)"
    @mouseleave="$emit('mouseleave', $event)"
  >
    <slot />
  </button>
</template>

<script>
/**
 * MDUI ÊåâÈíÆÁªÑ‰ª∂
 */
export default {
  name: 'MduiBtn',
  props: {
    // Ê∂üÊº™Âä®ÁîªÊïàÊûú
    ripple: { type: Boolean, default: false },
    // ÊµÆÂä®ÊåâÈíÆ
    raised: { type: Boolean, default: false },
    // ÂõæÊ†áÊåâÈíÆ
    icon: { type: Boolean, default: false },
    // ÂØÜÈõÜÂûãÊåâÈíÆ
    dense: { type: Boolean, default: false },
    // ÂùóÁ∫ßÂÖÉÁ¥†
    block: { type: Boolean, default: false },
    // Á¶ÅÁî®Áä∂ÊÄÅ
    disabled: { type: Boolean, default: false },
    // ËÉåÊôØÈ¢úËâ≤ mdui-color-[color]
    color: { type: String, default: '' },
    // ÊñáÊú¨È¢úËâ≤ mdui-text-color-[textColor]
    textColor: { type: String, default: '' }
  },
  computed: {
    mduiClass() {
      const c = {
        'mdui-ripple': this.ripple,
        'mdui-btn-raised': this.raised,
        'mdui-btn-icon': this.icon,
        'mdui-btn-dense': this.dense,
        'mdui-btn-block': this.block
      }
      if (this.color) {
        c['mdui-color-' + this.color] = true
      }
      if (this.textColor) {
        c['mdui-text-color-' + this.textColor] = true
      }
      return c
    }
  }
}
</script>
